.navBar {
  display: flex;
  justify-content: center;

  &__btn {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    padding: 4px;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    margin: 8px;
    border: 2px solid #9EC9F4;
  }

  &__btn-icon {
    margin-top: 8px;
  }

  &__btn-text {
    color: #9EC9F4;
  }
}