* {
  font-family: 'Montserrat', sans-serif;
}

.container-skills {
  text-align: center;
  margin-top: 70px;
  margin-bottom: 70px;
}

.skill-panel {
  display: inline-grid;
  grid-template-columns: auto auto auto auto;
  /* margin-top: 100px; */
}

.skill-items img {
  height: 70px;
  width: 70px;
  border: 1px;
  position: relative;
  /* margin: 2%; */
  overflow: hidden;
}

.skills-sets {
  /* border: 1px solid red; */
  height: 350px;
  width: 100%;
  display: flex;
  flex-direction: auto auto auto auto;
  justify-content: center;
  margin-bottom: 40px;
  /* line-height:200px; */
}

.skills-sets-items {
  height: 350px;
  width: 250px;
  /* border: 1px solid blue; */
  margin: 20px;
  border-radius: 5%;
  padding-top: 20px;
}

.skillbox {
  text-align: center;
}

.title-img {
  height: 110px;
  width: 110px;
  /* margin: 20px 120px; */
  margin-left: 85px;
  border-radius: 50%;
  text-align: center;
  align-items: center;
  background:#f7f7f2;
  -webkit-box-shadow:0 10px 6px -6px #777;
  -moz-box-shadow:0 10px 6px -6px #777;
  box-shadow:0 10px 6px -6px #777;
}
.title-sub {
  margin-top: 30px;
  margin-bottom: 30px;
  font-size: 23px;
  font-family: 'Montserrat', sans-serif;
}

.skill-list {
  list-style-type: none;
  /* border: 1px solid green; */
  text-align: left;
  margin-top: 50px;
  margin-right: 30px;
  /* margin-left: 6px; */
  font-size: 15px;
}

.oneline {
  margin-bottom: 80px;
}

li {
  padding-bottom: 4px;
}

.space {
  margin-left: 34px;
  margin-bottom: 4px;
}

.skill-img {
  padding-top: 30px;
  width: 50%;
}

.bullet {
  width: 7%;
  margin-right: 4px;
}




