    margin-bottom: 30px;
    font-size: 23px;
    font-family: 'Montserrat', sans-serif;
  }

  .skill-list {
    list-style-type: none;
    /* border: 1px solid green; */
    text-align: left;
    margin-top: 50px;
    margin-right: 30px;
    /* margin-left: 6px; */
    font-size: 15px;
  }

  .skills-sets-items {
    margin-left: 15%;
  }


  .oneline {
